.callback {
  @include wrapper;

  display: flex;
  justify-content: flex-end;
  padding: 70px 30px;

  font-family: $font-deafult;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  color: $gray--dark;

  background-image: url("../img/bg-callback.jpg");
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: 12px 0;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-callback-2x.jpg");
  }
}

.callback__container {
  width: 100%;
  max-width: 656px;
  margin-right: -12px;
  padding: 50px 49px 49px 50px;

  background-color: $white;
  border-radius: 5px;
  box-shadow: 0 4px 30px transparentize($gray--lighter, 0.9);
}

.callback__title {
  display: block;
  margin-bottom: 11px;

  font-size: 25px;
  line-height: 32px;
  font-weight: 600;
}

.callback__text {
  margin: 0;
  margin-bottom: 24px;
}

.callback__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.callback__form-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.callback__input {
  @include transition(border);

  width: 269px;
  margin-bottom: 25px;
  padding: 16px 15px 15px 14px;

  font-family: $font-deafult;
  font-size: 14px;
  line-height: 17px;
  color: $gray--dark;

  border: 1px solid transparentize($gray--light, 0.7);

  &-tel {
    padding: 16px 15px 15px 50px;

    background-image: url("../img/input-ph.svg");
    background-repeat: no-repeat;
    background-position: 14px 5px;
    background-size: auto 29px;
  }

  &::placeholder {
    font-family: $font-deafult;
    color: $gray--dark;
  }

  &:invalid {
    border: 1px solid $main;
  }

  &:hover,
  &:focus {
    border: 1px solid $gray--light;
  }
}

.callback__button {
  min-width: 176px;
  padding: 18px 10px 17px 10px;
}

@include tablet {
  .callback {
    justify-content: center;
    padding: 70px 0 75px 0;

    background-size: cover;
    background-position: 0 0;
  }

  .callback__container {
    margin-right: 0;
  }

  .callback__input {
    margin-bottom: 20px;
  }

  .callback__button {
    min-width: 249px;
    padding: 20px 10px 18px 10px;
  }
}

@include mobile {
  .callback {
    padding: 40px 0 0 0;

    background-image: none;
  }

  .callback__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 16px;

    background-color: $modal-bg;
    box-shadow: none;
  }

  .callback__title {
    margin-bottom: 20px;

    font-size: 25px;
    line-height: 32px;
    font-weight: 600;
  }

  .callback__form-wrapper {
    width: 288px;
  }

  .callback__text {
    max-width: 400px;
    margin-bottom: 19px;
  }

  .callback__form {
    width: 100%;
    max-width: 288px;
    margin-right: auto;
    margin-left: auto;
  }

  .callback__input {
    width: 100%;
  }

  .callback__button {
    width: 100%;
  }
}
